import React, { useState } from "react";

const stringToBinary = (str: string) => {
  const zeroPad = (num: number) => "00000000".slice(String(num).length) + num;
  return str.replace(
    /[\s\S]/g,
    str => zeroPad((str as any).charCodeAt(0).toString(2)) + " "
  );
};
